const callback = (entries, observer)=> {
    entries.forEach(entry => {
        //console.log(entry.target)
        //console.log(entry.time)
        //console.log(entry.isIntersecting)
        //console.log(entry.intersectionRatio)
        //console.log(entry.boundingClientRect)
        //console.log(entry.intersectionRect)
        //console.log(entry.rootBounds)

        if(entry.isIntersecting === true){
            console.log('test');
            entry.target.classList.add('is-show');
        }
    });
};

const options1 = { root: null, rootMargin: '30%', threshold: 1.0 }
const observer1 = new IntersectionObserver(callback, options1);
const target1 = document.querySelector('.section--1');
observer1.observe(target1);

const options2 = { root: null, rootMargin: '30%', threshold: 1.0 }
const observer2 = new IntersectionObserver(callback, options2);
const target2 = document.querySelector('.section--2');
observer2.observe(target2);

const options3 = { root: null, rootMargin: '30%', threshold: 1.0 }
const observer3 = new IntersectionObserver(callback, options3);
const target3 = document.querySelector('.section--3');
observer3.observe(target3);


